@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');

.links-grid-items {
    font-family: 'Roboto', sans-serif;
    display: grid;
    grid-template-columns: 50px 1fr;
    margin: 5px;
}

.links-buttons {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.links-qr-code > svg {
    width: 50px;
    height: 50px;
}
.links-body {
    margin-left: 5px;
}

.links-short-url {
    font-size: 13px;
    font-weight: 700;
}

.links-url {
    font-size: 13px;
    font-weight: 500;
    width: 60vw;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.links-date {
    font-size: 12px;
    font-weight: 400;
    opacity: 0.8;
}

.links-check-qr {
    font-size: 10px;
    margin: 0;
}

.links-qr-code-max {
    margin: 0 auto;
    width: 60vw;
}

.links-qr-code-max > svg {
    width: 60vw;
}

.links-qr-code-statistic {
    display: flex;
    font-size: 14px;
    opacity: 0.8;
    align-items: center;
    margin-left: 5px;
}

.links-qr-code-statistic-all {
    font-family: 'Roboto', sans-serif;
    font-weight: 600;
    font-size: 16px;
    margin: 5px 5px 10px 5px;
}

.links-qr-code-statistic-all-link {
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    font-size: 14px;
    opacity: 0.8;
    margin: 5px 5px 10px 5px;
}

.links-qr-code-statistic-all-date {
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    font-size: 13px;
    opacity: 0.8;
    margin: 5px;
}